<template>
  <v-card id="home-welcome-page">
    <v-layout>
      <v-sider :width="260" class="home-left-nav" style="background: #aaa;padding: 20px 0;">
        <v-tabs active-tab-key="/user" @change="onChange">
          <v-tab-pane tab-key="/user" tab="用户" style="font-size:20px;">
            <v-menu style="background: #aaa;" mode="inline" :data="userPages" @item-click="onClick"></v-menu>
          </v-tab-pane>
          <v-tab-pane tab-key="/manage" tab="管理">
            <v-menu style="background: #aaa;" mode="inline" :data="mngPages" @item-click="onClick"></v-menu>
          </v-tab-pane>
        </v-tabs>
      </v-sider>
      <v-content :style="{ padding: '24px' }">
        <router-view/>
      </v-content>
    </v-layout>
  </v-card>
</template>

<script>
export default {
  data () {
    return {
      userPages: [{
        name: '道具',
        selected: true
      },{
        name: '皮肤'
      },{
        name: '关卡'
      }],
      mngPages: [{
        name: '道具',
        selected: true
      },{
        name: '皮肤'
      },{
        name: '关卡'
      },{
        name: '游戏版本'
      }],
      toRoute: {
        '道具': '/props',
        '皮肤': '/skins',
        '关卡': '/levels',
        '游戏版本': '/versions'
      },
      urlType: "/user"
    }
  },
  created() {
    this.$router.push({path: '/user/props'});
  },
  methods: {
    onChange(tabKey) {
      this.urlType = tabKey;
    },
    onClick(itemKey) {
      this.$router.push({path: this.urlType+this.toRoute[itemKey[0].name]});
    }
  }
}
</script>

<style scoped>
#home-welcome-page {
  width: 100%;
  height: 100%;
}

#home-welcome-page .ant-layout {
  height: 100%;
}
</style>



